import React, { Component } from 'react';
import './style.scss';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import md5 from 'md5';
import { FormControl, InputLabel, Input, FormHelperText, Select, MenuItem } from '@material-ui/core';
import PageHeader from '../../components/PageHeader';
import { createMemberAction, resetCreateMember } from './action';
import Button from '../../components/Button';
import { generateRandomNumber } from '../../utilities/generateRandomNumber';
import { charsRegex, emailRegex, numberRegex } from '../../config/regex';

class ResetPassword extends Component {

    state = {
        fields: {
            name: "",
            email: "",
            password: "",
            contact: "",
            role: "",
            sum: ""
        },
        errors: {
            name: "",
            email: "",
            password: "",
            contact: "",
            role: "",
            sum: ""
        },
        enableSubmit: false
    }

    componentDidMount() {
        let { fields } = this.state;
        fields.login_id = this.props.login_id;
        this.setState({ fields });
        this.genarateNumbers();
    }

    componentWillReceiveProps(props) {
        if (props.response) {
            let fields = {
                name: "",
                email: "",
                password: "",
                contact: "",
                role: "",
                sum: ""
            }
            this.setState({ fields, enableSubmit: false }, () => {
                this.genarateNumbers();
                // this.props.resetCreateMember();
            });
        }
    }

    componentWillUnmount() {
        this.props.resetCreateMember();
    }

    genarateNumbers = () => {
        this.setState({
            number1: generateRandomNumber(0, 9),
            number2: generateRandomNumber(0, 9)
        });
    }

    checkValidityOfForm = () => {
        let { fields, errors } = this.state;
        let hasErrors = false, validForm = false;
        let requiredFields = ["name", "email", "password", "contact", "role", "sum"];
        validForm = requiredFields.every(field => fields[field] !== "");
        hasErrors = Object.keys(errors).length !== 0 && requiredFields.some(field => errors[field] && errors[field] !== "");
        this.setState({
            enableSubmit:
                !hasErrors &&
                validForm &&
                Number(this.state.number1) + Number(this.state.number2) === Number(this.state.fields.sum)
        });
    }

    handleChange = (event) => {
        let fieldName = event.target.name;
        let fieldValue = event.target.value;

        let { fields, errors } = this.state;

        switch (fieldName) {
            case "name":
                if (charsRegex.test(fieldValue) && fieldValue.length > 2) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (fieldValue && charsRegex.test(fieldValue)) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Name should be greater than 2 characters";
                } else if (fieldValue === "") {
                    fields[fieldName] = "";
                    errors[fieldName] = "Name field Required!";
                }
                break;

            case "email":
                if (fieldValue && emailRegex.test(fieldValue)) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (fieldValue && !emailRegex.test(fieldValue)) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Inavlid Email Address";
                } else if (fieldValue === "") {
                    fields[fieldName] = "";
                    errors[fieldName] = "Email field Required!";
                }
                break;

            case "password":
                if (fieldValue) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                    // } else if (fieldValue && charsRegex.test(fieldValue)) {
                    //     fields[fieldName] = fieldValue;
                    //     errors[fieldName] = "Name should be greater than 2 characters";
                } else if (fieldValue === "") {
                    fields[fieldName] = "";
                    errors[fieldName] = "Password field Required!";
                }
                break;

            case "contact":
                if (fieldValue && numberRegex.test(fieldValue) && fieldValue.length === 10) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "";
                } else if (fieldValue && numberRegex.test(fieldValue) && fieldValue.length < 10) {
                    fields[fieldName] = fieldValue;
                    errors[fieldName] = "Invalid Contact Number";
                } else if (!fieldValue) {
                    fields[fieldName] = "";
                    errors[fieldName] = "Contact Number field Required!";
                }
                break;

            case 'sum':
                let numberRegexWithLength2 = /^[0-9]{0,2}$/;
                if (numberRegexWithLength2.test(fieldValue)) {
                    fields[fieldName] = numberRegexWithLength2.test(fieldValue) ? fieldValue : fields[fieldName];
                    if (this.state.number1 + this.state.number2 === Number(fieldValue)) {
                        errors[fieldName] = '';
                    } else {
                        errors[fieldName] = 'Invalid Sum';
                    }
                } else if (!fieldValue) {
                    fields[fieldName] = '';
                    errors[fieldName] = 'Required';
                }
                break;

            default: fields[fieldName] = fieldValue;
        }

        this.setState({ fields, errors }, () => this.checkValidityOfForm());
    }

    generatePassword = () => {
        let { fields } = this.state;
        fields.password = Math.random() // Generate random number, eg: 0.123456
            .toString(36)               // Convert  to base-36 : "0.4fzyo82mvyr"
            .slice(-8);                 // Cut off last 8 characters : "yo82mvyr"
        this.setState({ fields });
    }

    createMember = () => {
        let { fields } = this.state;
        let payload = { ...fields };
        payload.password = md5(fields.password);
        this.props.createMemberAction(payload);
    }

    render() {
        const title = "Create Member";
        let { fields, errors } = this.state
        return (
            <div className="create-member-container">
                <PageHeader
                    header={title}
                />
                <form className="change-password-form">

                    <FormControl className="full-width form-control-custom" error={errors.name}>
                        <InputLabel htmlFor="name">Name</InputLabel>
                        <Input
                            id="name"
                            name="name"
                            type="text"
                            value={fields.name}
                            onChange={this.handleChange}
                            aria-describedby="name"
                            fullWidth={true}
                        />
                        <FormHelperText id="name">{errors.name}</FormHelperText>
                    </FormControl>
                    <br />

                    <FormControl className="full-width form-control-custom" error={errors.email}>
                        <InputLabel htmlFor="email">Email</InputLabel>
                        <Input
                            id="email"
                            name="email"
                            type="text"
                            value={fields.email}
                            onChange={this.handleChange}
                            aria-describedby="email"
                            fullWidth={true}
                        />
                        <FormHelperText id="email">{errors.email}</FormHelperText>
                    </FormControl>
                    <br />

                    <div className="password-container">
                        <FormControl className="full-width form-control-custom" error={errors.password}>
                            <InputLabel htmlFor="password">Password</InputLabel>
                            <Input
                                id="password"
                                name="password"
                                type="text"
                                value={fields.password}
                                onChange={this.handleChange}
                                aria-describedby="password"
                                fullWidth={true}
                            />
                            <FormHelperText id="password">{errors.password}</FormHelperText>
                        </FormControl>
                        <Button
                            label="Generate Password"
                            onClick={this.generatePassword}
                        />
                    </div>
                    <br />

                    <FormControl className="full-width form-control-custom" error={errors.contact}>
                        <InputLabel htmlFor="contact">Contact Number</InputLabel>
                        <Input
                            id="contact"
                            name="contact"
                            type="contact"
                            value={fields.contact}
                            onChange={this.handleChange}
                            aria-describedby="contact"
                            fullWidth={true}
                        />
                        <FormHelperText id="contact">{errors.contact}</FormHelperText>
                    </FormControl>
                    <br />

                    <FormControl className="full-width form-control-custom" error={errors.role}>
                        <InputLabel htmlFor="role">Role</InputLabel>
                        <Select
                            id="role"
                            name="role"
                            value={fields.role}
                            onChange={this.handleChange}
                            aria-describedby="role"
                            fullWidth={true}
                        >
                            <MenuItem value={"cet"}>CET</MenuItem>
                            <MenuItem value={"sales"}>Sales</MenuItem>
                        </Select>
                        <FormHelperText id="role">{errors.role}</FormHelperText>
                    </FormControl>
                    <br />

                    <div className="number-container">
                        <FormControl className="full-width form-control-custom">
                            <InputLabel htmlFor="number-one"></InputLabel>
                            <Input
                                id="number-one"
                                // name="number1"
                                // type="number"
                                value={this.state.number1}
                                // onChange={this.handleChange}
                                aria-describedby="number-one-text"
                                fullWidth={true}
                                disabled={true}
                            />
                            <FormHelperText id="number-one-text"></FormHelperText>
                        </FormControl>

                        <div className="plus-symbol">+</div>

                        <FormControl className="full-width form-control-custom">
                            <InputLabel htmlFor="number-two"></InputLabel>
                            <Input
                                id="number-two"
                                // name="number2"
                                // type="number"
                                value={this.state.number2}
                                // onChange={this.handleChange}
                                aria-describedby="number-two-text"
                                fullWidth={true}
                                disabled={true}
                            />
                            <FormHelperText id="number-two-text"></FormHelperText>
                        </FormControl>

                        <div className="plus-symbol">=</div>

                        <FormControl className="full-width form-control-custom" error={errors.sum}>
                            <InputLabel htmlFor="sum">Result</InputLabel>
                            <Input
                                id="sum"
                                name="sum"
                                // type="text"
                                value={fields.sum}
                                maxLength="2"
                                onChange={this.handleChange}
                                aria-describedby="sum-text"
                                fullWidth={true}
                            />
                            <FormHelperText id="sum-text">{errors.sum}</FormHelperText>
                        </FormControl>
                    </div>

                    <div className="button-container">
                        <Button
                            label="Create Member"
                            disabled={!this.state.enableSubmit}
                            onClick={this.createMember}
                        />
                    </div>
                </form>
            </div>
        );
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
        response: state.createMemberData.response
    }
}

const mapDispatchToProps = (dispatch) => {
    return bindActionCreators({ createMemberAction, resetCreateMember }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(ResetPassword);
