import { CREATE_MEMBER_SUCCESS, RESET_CREATE_MEMBER } from "./constants";
import { showLoader, hideLoader } from "../../components/Loader/action";
import handleError from "../../errorHandler";
import { createMember } from "../../api";
import { toast } from "react-toastify";

export function createMemberAction(data) {
    return (dispatch) => {
        dispatch(showLoader());
        createMember(data)
            .then(response => {
                dispatch(hideLoader());
                dispatch(createMemberSuccess(response.data));
                toast.success("Member Created Successfully!!!");
                // toast.success(response.data.message);
            })
            .catch(error => {
                handleError(error, dispatch);
            });
    }
}

export function createMemberSuccess(data) {
    return {
        type: CREATE_MEMBER_SUCCESS,
        data
    }
}

export function resetCreateMember() {
    return {
        type: RESET_CREATE_MEMBER
    }
}
